import React, {useEffect, useRef, useState} from "react";
import Tables from './components/Tables'

import Tabletop from "tabletop";
import "bootstrap/dist/css/bootstrap.min.css";
import "./shards-dashboard/styles/shards-dashboards.1.1.0.min.css";
import CollapsibleTable from "./components/CollapsibleTable.js"
function App() {
  const [isLoading, setLoading] = React.useState(true);
  const [googleData, setGoogleData] = React.useState(null);
  const [data, setData] = React.useState(null);
  const [deptData, setDeptData] = React.useState(null);
  useEffect(() => {
    Tabletop.init({
      key: "13KkkMk2pnHmS3WFCgXYtQ_yvbyOa0UuG2Za8P2nQI8s",
      callback: (googleData) => {
        console.log("google sheet data --->", googleData);
        var show_act_data = []; //แสดงกิจกรรมหน้าแรก
        var i = 0;
        var data = [];
        var depts = [];
        var itemThai = [];
        var itemMath = [];
        var itemST = [];
        var itemSocial = [];
        var itemPS = [];
        var itemHome = [];
        var itemArt = [];
        var itemEng = [];
        var itemNa = [];
        googleData.map((obj, index) => {
          data.push(obj);
          depts.push(obj.dept);
          if (obj.dept == "ภาษาไทย") {
            itemThai.push(obj);
          }
          else if (obj.dept == "คณิตศาสตร์") {
            itemMath.push(obj);
          }
          else if (obj.dept == "วิทยาศาสตร์และเทคโนโลยี") {
            itemST.push(obj);
          }
          else if (obj.dept == "สังคมศึกษาฯ") {
            itemSocial.push(obj);
          }
          else if (obj.dept == "สุขศึกษาและพลศึกษา") {
            itemPS.push(obj);
          }
          else if (obj.dept == "ศิลปะ") {
            itemArt.push(obj);
          }
          else if (obj.dept == "การงานอาชีพ") {
            itemHome.push(obj);
          }
          else if (obj.dept == "ภาษาต่างประเทศ") {
            itemEng.push(obj);
          }
          else {
            itemNa.push(obj);
          }
        });
        var a = [];
        a.push(itemThai);
        a.push(itemMath);
        a.push(itemST);
        a.push(itemSocial);
        a.push(itemPS);
        a.push(itemArt);
        a.push(itemHome);
        a.push(itemEng);
        a.push(itemNa);
        setGoogleData(data);
        setData(a);
        setLoading(false);
      },
      simpleSheet: true,
    });
  }, []);

  return (
    !isLoading && (
      <div>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-success" title={"กลุ่มสาระการเรียนรู้ภาษาไทย"} data = {data[0]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-warning" title={"กลุ่มสาระการเรียนรู้คณิตศาสตร์"}  data = {data[1]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-info" title={"กลุ่มสาระการเรียนรู้วิทยาศาสตร์และเทคโนโลยี"}  data = {data[2]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-danger" title={"กลุ่มสาระการเรียนรู้สังคมศึกษาฯ"}  data = {data[3]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-secondary" title={"กลุ่มสาระการเรียนรู้สุขศึกษาและพลศึกษา"}  data = {data[4]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-success" title={"กลุ่มสาระการเรียนรู้การงานอาชีพ"}  data = {data[5]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-dark" title={"กลุ่มสาระการเรียนรู้ศิลปะ"}  data = {data[6]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-primary" title={"กลุ่มสาระการเรียนรู้ภาษาต่างประเทศ"}  data = {data[7]}></Tables>
    <Tables classText="m-0 text-white" classColor="border-bottom bg-warning" title={"กลุ่มงานสนับสนุนการศึกษา"}  data = {data[8]}></Tables>
    </div>
    )
  )
}

export default App